'use client';

import React, { useState, useEffect } from 'react';
import { useTimeZoneDetection } from '@/hooks/useTimeZoneDetection';
import { getTimeZoneCode, type SupportedLanguage } from '@/utils/date';
import { DateTime } from 'luxon';

/**
 * ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤Ω ÎîîÎ≤ÑÍπÖÏùÑ ÏúÑÌïú Ïª¥Ìè¨ÎÑåÌä∏ (Luxon Í∏∞Î∞ò)
 */
export function TimeZoneDebug() {
  const { timeZone, offset, changed, checkTimeZone } = useTimeZoneDetection();
  const [manualCheck, setManualCheck] = useState('');
  
  // ÏàòÎèô Ï≤¥ÌÅ¨ Ìï®Ïàò
  const performManualCheck = () => {
    const browserTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
    const browserOffset = new Date().getTimezoneOffset();
    
    // ÌÖåÏä§Ìä∏Ìï† ÏãúÍ∞ÑÎåÄÎì§
    const testTimeZones = [browserTimeZone, 'Asia/Seoul', 'Asia/Tokyo', 'Asia/Kolkata', 'America/New_York'];
    
    // Ïö∞Î¶¨ Ìï®Ïàò Í≤∞Í≥º (Ï£ºÏöî ÏãúÍ∞ÑÎåÄÎäî ÌëúÏ§Ä ÏïΩÏñ¥ ÏÇ¨Ïö©)
    const luxonResults = testTimeZones.map(tz => {
      try {
        const abbr = getTimeZoneCode(tz, 'ko' as SupportedLanguage);
        const dt = DateTime.now().setZone(tz);
        const offsetHours = dt.offset / 60;
        const offsetStr = offsetHours >= 0 ? `+${offsetHours}` : `${offsetHours}`;
        return `${tz}: ${abbr} (UTC${offsetStr})`;
      } catch (e) {
        return `${tz}: Ìï®Ïàò Ïò§Î•ò`;
      }
    }).join('\n      ');
    
    // ÌëúÏ§Ä API Í≤∞Í≥º
    const apiResults = testTimeZones.map(tz => {
      try {
        const formatter = new Intl.DateTimeFormat('ko-KR', {
          timeZone: tz,
          timeZoneName: 'short'
        });
        const parts = formatter.formatToParts(new Date());
        const tzPart = parts.find(part => part.type === 'timeZoneName');
        const rawValue = tzPart?.value || '?';
        return `${tz}: ${rawValue}`;
      } catch (e) {
        return `${tz}: API Ïò§Î•ò`;
      }
    }).join('\n      ');
    
    // Ïö∞Î¶¨ Ìï®Ïàò Í≤∞Í≥º (Luxon + API Ìè¥Î∞±)
    const ourFunctionResults = testTimeZones.map(tz => {
      try {
        const code = getTimeZoneCode(tz, 'ko' as SupportedLanguage);
        return `${tz}: ${code}`;
      } catch (e) {
        return `${tz}: Ìï®Ïàò Ïò§Î•ò`;
      }
    }).join('\n      ');

    setManualCheck(`
      Î∏åÎùºÏö∞Ï†Ä Ï†ïÎ≥¥:
      - ÏãúÍ∞ÑÎåÄ: ${browserTimeZone}
      - Ïò§ÌîÑÏÖã: ${browserOffset}Î∂Ñ
      - ÌòÑÏû¨ ÏãúÍ∞Ñ: ${new Date().toLocaleString()}
      
      ÏãúÍ∞ÑÎåÄÎ≥Ñ ÌòÑÏû¨ ÏãúÍ∞Ñ:
      - ÌïúÍµ≠: ${new Date().toLocaleString('ko-KR', { timeZone: 'Asia/Seoul' })}
      - ÏùºÎ≥∏: ${new Date().toLocaleString('ko-KR', { timeZone: 'Asia/Tokyo' })}
      - Ïù∏ÎèÑ: ${new Date().toLocaleString('ko-KR', { timeZone: 'Asia/Kolkata' })}
      - ÎØ∏Íµ≠ ÎèôÎ∂Ä: ${new Date().toLocaleString('ko-KR', { timeZone: 'America/New_York' })}
      - UTC: ${new Date().toLocaleString('ko-KR', { timeZone: 'UTC' })}
      
      === Ïö∞Î¶¨ getTimeZoneCode() Ìï®Ïàò Í≤∞Í≥º (Ï£ºÏöî ÏãúÍ∞ÑÎåÄ ÌëúÏ§Ä ÏïΩÏñ¥) ===
      ${luxonResults}
      
      === ÌëúÏ§Ä API timeZoneName: 'short' Í≤∞Í≥º ===
      ${apiResults}
      
      === Ïö∞Î¶¨ getTimeZoneCode() Ìï®Ïàò Í≤∞Í≥º ===
      ${ourFunctionResults}
    `);
    
    checkTimeZone();
  };

  // Ìè¨Ïª§Ïä§ Ïù¥Î≤§Ìä∏ Í∞ïÏ†ú Ìä∏Î¶¨Í±∞
  const forceFocusEvent = () => {
    console.log('üîß Ìè¨Ïª§Ïä§ Ïù¥Î≤§Ìä∏ Í∞ïÏ†ú Ìä∏Î¶¨Í±∞');
    window.dispatchEvent(new Event('focus'));
  };

  // Í∞ÄÏãúÏÑ± Ïù¥Î≤§Ìä∏ Í∞ïÏ†ú Ìä∏Î¶¨Í±∞
  const forceVisibilityEvent = () => {
    console.log('üîß Í∞ÄÏãúÏÑ± Ïù¥Î≤§Ìä∏ Í∞ïÏ†ú Ìä∏Î¶¨Í±∞');
    Object.defineProperty(document, 'visibilityState', {
      value: 'visible',
      writable: true
    });
    document.dispatchEvent(new Event('visibilitychange'));
  };
  
  return (
    <div className="p-4 bg-gray-100 rounded-lg max-w-2xl mx-auto">
      <h2 className="text-xl font-bold mb-4">üîç ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤Ω ÎîîÎ≤ÑÍπÖ (Luxon Í∏∞Î∞ò - ÌïòÎìúÏΩîÎî© ÏóÜÏùå!)</h2>
      
      {/* ÌòÑÏû¨ ÌõÖ ÏÉÅÌÉú */}
      <div className="mb-4 p-3 bg-blue-50 rounded">
        <h3 className="font-semibold">ÌòÑÏû¨ ÌõÖ ÏÉÅÌÉú:</h3>
        <div className="mt-2 text-sm font-mono">
          <div>ÏãúÍ∞ÑÎåÄ: <span className={changed ? 'text-red-600' : 'text-green-600'}>{timeZone}</span></div>
          <div>Ïò§ÌîÑÏÖã: {offset}Î∂Ñ</div>
          <div>Î≥ÄÍ≤ΩÎê®: {changed ? '‚úÖ Ïòà' : '‚ùå ÏïÑÎãàÏò§'}</div>
          <div>ÏãúÍ∞ÑÎåÄ ÏΩîÎìú: <span className="bg-yellow-200 px-1 rounded">{getTimeZoneCode(timeZone, 'ko' as SupportedLanguage)}</span></div>
          <div className="mt-2 text-xs text-gray-600">
            Luxon ÏßÅÏ†ë Ìò∏Ï∂ú: {getTimeZoneCode(timeZone, 'ko' as SupportedLanguage)}
          </div>
        </div>
      </div>
      
      {/* ÏàòÎèô Ï≤¥ÌÅ¨ Î≤ÑÌäºÎì§ */}
      <div className="mb-4 space-x-2">
        <button 
          onClick={performManualCheck}
          className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
        >
          ÏàòÎèô ÏãúÍ∞ÑÎåÄ Ï≤¥ÌÅ¨
        </button>
        <button 
          onClick={forceFocusEvent}
          className="px-4 py-2 bg-green-500 text-white rounded hover:bg-green-600"
        >
          Ìè¨Ïª§Ïä§ Ïù¥Î≤§Ìä∏ Ìä∏Î¶¨Í±∞
        </button>
        <button 
          onClick={forceVisibilityEvent}
          className="px-4 py-2 bg-purple-500 text-white rounded hover:bg-purple-600"
        >
          Í∞ÄÏãúÏÑ± Ïù¥Î≤§Ìä∏ Ìä∏Î¶¨Í±∞
        </button>
      </div>
      
      {/* ÏàòÎèô Ï≤¥ÌÅ¨ Í≤∞Í≥º */}
      {manualCheck && (
        <div className="mb-4 p-3 bg-yellow-50 rounded">
          <h3 className="font-semibold">ÏàòÎèô Ï≤¥ÌÅ¨ Í≤∞Í≥º:</h3>
          <pre className="mt-2 text-xs font-mono whitespace-pre-wrap">{manualCheck}</pre>
        </div>
      )}
      
      {/* Luxon vs ÌëúÏ§Ä API ÎπÑÍµê */}
      <div className="mb-4 p-3 bg-green-50 rounded">
        <h3 className="font-semibold">Luxon vs ÌëúÏ§Ä API ÎπÑÍµê:</h3>
        <div className="mt-2 text-sm">
          <div>Ïö∞Î¶¨ Ìï®Ïàò: {getTimeZoneCode(timeZone, 'ko' as SupportedLanguage)}</div>
          <div>Luxon ÏõêÏãúÍ∞í: {timeZone !== 'UTC' ? DateTime.now().setZone(timeZone).offsetNameShort : 'UTC'}</div>
          <div className="text-xs text-gray-600 mt-1">
            ‚ú® Ï£ºÏöî ÏãúÍ∞ÑÎåÄÎäî ÌëúÏ§Ä ÏïΩÏñ¥ ÏÇ¨Ïö©, ÎÇòÎ®∏ÏßÄÎäî Intl API Ìè¥Î∞±!
          </div>
        </div>
      </div>
      
      {/* ÌÖåÏä§Ìä∏ ÏïàÎÇ¥ */}
      <div className="p-3 bg-gray-50 rounded">
        <h3 className="font-semibold">ÌÖåÏä§Ìä∏ Î∞©Î≤ï:</h3>
        <ol className="mt-2 text-sm list-decimal list-inside">
          <li>Îß• ÏãúÏä§ÌÖú ÌôòÍ≤ΩÏÑ§Ï†ïÏóêÏÑú ÏãúÍ∞ÑÎåÄÎ•º Îã§Î•∏ ÏßÄÏó≠(Ïòà: Ïù∏ÎèÑ)ÏúºÎ°ú Î≥ÄÍ≤Ω</li>
          <li>Îã§Î•∏ Ïï±(Ïòà: Finder)ÏùÑ ÌÅ¥Î¶≠ÌñàÎã§Í∞Ä Îã§Ïãú Î∏åÎùºÏö∞Ï†ÄÎ°ú ÎèåÏïÑÏò§Í∏∞</li>
          <li>ÎòêÎäî &quot;ÏàòÎèô ÏãúÍ∞ÑÎåÄ Ï≤¥ÌÅ¨&quot; Î≤ÑÌäº ÌÅ¥Î¶≠</li>
          <li>ÏΩòÏÜî Î°úÍ∑∏ ÌôïÏù∏ (F12 ‚Üí Console)</li>
          <li>ÏãúÍ∞ÑÎåÄ ÏΩîÎìúÍ∞Ä Ï†ïÌôïÌûà ÌëúÏãúÎêòÎäîÏßÄ ÌôïÏù∏ (Ïòà: IST, KST Îì±)</li>
          <li>üéâ KST, JST Îì± ÌëúÏ§Ä ÏïΩÏñ¥Í∞Ä Ï†ïÌôïÌûà ÌëúÏãúÎêòÎäîÏßÄ ÌôïÏù∏!</li>
        </ol>
      </div>
      
      {/* Î≥ÄÍ≤Ω ÏïåÎ¶º */}
      {changed && (
        <div className="mt-4 p-3 bg-red-100 border border-red-400 rounded">
          <strong>üö® ÏãúÍ∞ÑÎåÄ Î≥ÄÍ≤Ω Í∞êÏßÄÎê®!</strong>
          <div className="mt-1 text-sm">
            ÏÉàÎ°úÏö¥ ÏãúÍ∞ÑÎåÄ: {timeZone} ({getTimeZoneCode(timeZone, 'ko' as SupportedLanguage)})
          </div>
        </div>
      )}
    </div>
  );
}

export default TimeZoneDebug; 